import intl from "react-intl-universal";
import _ from "lodash";
import axios from "axios";
import React, { Component } from "react";
import PluralComponent from "./Plural";
import BasicComponent from "./Basic";
import HtmlComponent from "./Html";
import DateComponent from "./Date";
import CurrencyComponent from "./Currency";
import MessageNotInComponent from "./MessageNotInComponent";
import "./app.css";

const SUPPOER_LOCALES = [
  {
    name: "English",
    value: "en-US"
  },
  {
    name: "",
    value: "zh-CN"
  },
  {
    name: "",
    value: "zh-TW"
  },
  {
    name: "franais",
    value: "fr-FR"
  },
  {
    name: "",
    value: "ja-JP"
  }
];

class App extends Component {
  constructor(props) {
    super(props);
    this.onSelectLocale = this.onSelectLocale.bind(this);
    this.state = {
      initDone: false, 
      currentLocale:"en-US" //  
    }
  }

  componentDidMount() {
    //  
    this.loadLocales();
  }

  render() {
    return (
      this.state.initDone &&
      <div>
        {console.log(this.state.currentLocale +'____')}
        {this.renderLocaleSelector()}
        <BasicComponent />
        <PluralComponent />
        <HtmlComponent />
        <DateComponent />
        <CurrencyComponent />
        <MessageNotInComponent />
      </div>
    );
  }
  
  //  
  certify(currentLocale){
    return currentLocale && 
      axios
    .get(`locales/${currentLocale}.json`)
    .then(res => {
     //  Api 
      return intl.init({
        currentLocale,
        locales: {
          [currentLocale]: res.data
        }
      });
    })
  }
  
  // 
  loadLocales() {
   let { currentLocale } = this.state; // 
   this.certify(currentLocale)
      .then(() => {
        //  render  
        this.setState({ initDone: true });
      });
  }

  // 
  renderLocaleSelector() {
    return (
      <select onChange={ this.onSelectLocale.bind(this) } defaultValue="">
        <option value="" disabled>Change Language</option>
        {SUPPOER_LOCALES.map(locale => (
          <option key={locale.value} value={locale.value}>{locale.name}</option>
        ))}
      </select>
    );
  }
 // 
  onSelectLocale(e) { 
    let currentLocale = e.target.value;
    this.certify(currentLocale)
      .then( () => {
        //   
        this.setState({
          currentLocale:currentLocale
        })
      })
  }
}

export default App;



